import React, { Component } from 'react';
import { Input } from 'semantic-ui-react';
import MaterialBtn from '@material-ui/core/Button';
import Head from 'next/head'
import isEmail from 'is-email'
import helper from '../server/config'
import axios from 'axios'

class SignupPage extends Component {
    constructor(props) {
        super(props);
        this.state = {  };
    }

    handleInput = (e) => {
        let target = e.target;
        let name = target.name;
        let value = target.value;
        this.setState({[name]:value}, () => console.log(this.state[name]))
    }


    emailValitade = (email) => {
        return isEmail(email)
    }

    signup = () => {
        const isEmail = this.emailValitade(this.state.email);
        console.log(isEmail)
        if (this.state.email && isEmail && this.state.password && this.state.password.length >= 6) {
            axios.post(`${helper.server}/signup`, {
                username:this.state.email,
                password:this.state.password
            })
            .then(res => {
                if (res.data.code == 11000) {
                    console.log('email exist')
                }
                console.log(res.data)
            }) 
            .catch(e => console.log(e))
        } else {
            console.log('couldnt do it')
        }
    }

    render() {
        return (
            <div >
            {style}
            <Head>
                <link rel="stylesheet" href="//cdnjs.cloudflare.com/ajax/libs/semantic-ui/2.3.3/semantic.min.css"></link>
            </Head>
                <div className='center'>  
                    <div >
                        <Input onChange={(e) => this.handleInput(e)} name='email' placeholder='email' className='center-input'/>                
                    </div>
                    <div >
                        <Input onChange={(e) => this.handleInput(e)} name='password' type='password' placeholder='password' className='center-input'/>                
                    </div>
                    <div >
                        <MaterialBtn onClick={this.signup} className='center-input'>Signup</MaterialBtn>                
                    </div>
                </div>
            </div>
        );
    }
}



let style = (
    <style jsx='true'>
        {`
            .center-input {
                margin: 0 auto;
                width:100%;
                margin-top:0.5rem;
            }
            .center {
                width:40%;
                margin-left:auto;
                margin-right:auto;
                margin-top:5rem;
            }
        `}
    </style>
)

export default SignupPage;


